<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="8">
          <b-card-group>
            <b-card no-body class="p-4">
              <b-card-body>
                <b-form>
                  <b-link :to="{name:'public'}">
                    <div class="text-center mb-4" style="margin-top:-20px">
                      <img src="img/logo-bbm.png" height="100" alt="BBM Field Force">
                    </div>
                  </b-link>
                  <!-- <div class="text-center mb-4" style="margin-top:-20px">
                                        <img src="img/logo-sinau.png" height="100" alt="CoreUI Logo">
                  </div>-->
                  <p class="text-muted">Log In BBM Field Force</p>
                  <b-input-group class="mb-3">
                    <b-input-group-prepend>
                      <b-input-group-text>
                        <i class="icon-user"></i>
                      </b-input-group-text>
                    </b-input-group-prepend>
                    <b-form-input
                      type="text"
                      class="form-control"
                      ref="username"
                      v-model="model.username"
                      placeholder="Username"
                      autocomplete="username"
                    />
                  </b-input-group>
                  <b-input-group class="mb-4">
                    <b-input-group-prepend>
                      <b-input-group-text>
                        <i class="icon-lock"></i>
                      </b-input-group-text>
                    </b-input-group-prepend>
                    <b-form-input
                      type="password"
                      class="form-control"
                      ref="password"
                      v-model="model.password"
                      placeholder="Password"
                      autocomplete="current-password"
                    />
                  </b-input-group>
                  <b-row>
                    <b-col cols="6">
                      <b-button variant="primary" class="px-4" @click="login()">Login</b-button>
                      <!-- <b-button variant="primary" class="px-4" @click="me()">Me</b-button> -->
                    </b-col>
                    <b-col cols="6" class="text-right">
                      <!-- <b-button variant="link" class="px-0">Forgot password?</b-button> -->
                    </b-col>
                  </b-row>
                </b-form>
              </b-card-body>
            </b-card>
            <b-card no-body class="text-white bg-primary py-5 d-md-down-none" style="width:44%">
              <b-card-body class="text-center">
                <div>
                  <br>
                  <br>
                  <br>
                  <!-- <b-button variant="primary" class="active mt-4" @click="register()">Register Now!</b-button> -->
                </div>
              </b-card-body>
            </b-card>
          </b-card-group>
        </b-col>
      </b-row>
    </div>
  </div>
</template>


<script>
import auth from "../apis/auth";

export default {
  data() {
    return {
      //   model: { username: "admin", password: "admin-123" },
      model: {},
      clientDisabled: false
    };
  },
  created() {
    const { $route } = this;
    const { query } = $route;

    if (query && query.clientId) {
      this.model.client = query.clientId;
      this.clientDisabled = true;
    }
  },
  beforeCreate() {
    const { $router, $store } = this;
    const { user } = $store.state;

    if (user) {
      const { roles } = user;
      if (roles === "admin") $router.push({ name: "admin" });
      if (roles === "member") $router.push({ name: "app" });
    }
  },
  methods: {
    login: async function() {
      const { $router, $store } = this;
      const { username, password } = this.model;

      if (!username) return;
      if (!password) return;

      try {
        const tk = await auth.validate(this.model);
        const me = await auth.current();
        auth.save(tk, me);

        $store.commit("user", me);

        if (me.roles === "admin") {
          $router.push({ name: "admin" });
        } else {
          $router.push({ name: "app" });
        }
      } catch (error) {
        console.log(error);
      }
    },
    me: async function() {
      const me = await this.$http.get("users/me");
      console.log(me);
    },
    register() {
      this.$router.push({ name: "page.register" });
    }
  }
};
</script>
