import axios from 'axios';
import helpers from './helpers';
import uuid from 'uuid';

function list(options) {
    return new Promise(async(resolve, reject) => {
        const obj = options || {};
        try {
            const keys = ['page', 'limit', 'sortBy', 'sortDir', 'filter'];

            let str = '';
            for (const key in obj) {
                if (keys.indexOf(key) >= 0 && obj[key]) {
                    str += str ? '&' : '?';
                    str += key + '=' + encodeURIComponent(obj[key]);
                }
            }

            const url = `/stores/list${str}`;
            const { data } = await axios.get(url);
            const { meta = {}, list } = data;
            obj.total = meta.total;
            resolve(list);
        } catch (error) {
            reject(error);
        }
    });
}

function getDetail(id) {
    return new Promise(async(resolve, reject) => {
        try {
            const url = `/stores/${id}`;
            const { data } = await axios.get(url);
            resolve(data);
        } catch (error) {
            helpers.errorResponse(error, reject);
        }
    });
};

function getMajorSubject(majorId) {
    return new Promise(async(resolve, reject) => {
        try {
            const url = `/subject/allocation_grid/${majorId}`;
            const { data } = await axios.get(url);
            const { list } = data;
            resolve(list);
        } catch (error) {
            helpers.errorResponse(error, reject);
        }
    });
};

function dropDown() {
    return new Promise(async(resolve, reject) => {
        try {
            const { data } = await axios.get('/subject/dropdown');
            resolve(data);
        } catch (error) {
            reject(error);
        }
    });
}

function insert(item) {
    return new Promise(async(resolve, reject) => {
        try {
            delete item.area_tmp;
            item.store_id = uuid();
            item.created_at = new Date();
            const url = '/stores';

            const { data } = await axios.post(url, item);
            resolve(data);
        } catch (error) {
            helpers.errorResponse(error, reject);
        }
    });
};

function update(id, item) {
    return new Promise(async(resolve, reject) => {
        try {
            const url = `/stores/${id}`;
            delete item.created_at;
            delete item.area_tmp;

            item.updated_at = new Date();

            const { data } = await axios.put(url, item);
            resolve(data);
        } catch (error) {
            helpers.errorResponse(error, reject);
        }
    });
};

function remove(id) {
    return new Promise(async(resolve, reject) => {
        try {
            const url = `/stores/${id}`;
            const { data } = await axios.delete(url);
            resolve(data);
        } catch (error) {
            helpers.errorResponse(error, reject);
        }
    });
};

export default { list, getDetail, dropDown, insert, update, remove, getMajorSubject }