  <div class="app">
    <AppHeader fixed>
      <SidebarToggler class="d-lg-none" display="md" mobile/>
      <b-link class="navbar-brand" :to="{name: 'public'}">
        <img class="navbar-brand-full logo" src="img/logo-bbm.png">
      </b-link>
      <!-- <SidebarToggler class="d-md-down-none" display="lg"/> -->
      <b-navbar-nav class="d-md-down-none">
        <b-dropdown variant="link" text="Shortcut Menus" class="m-2">
          <div class="shortcut-link p-3 mt-2 mb-2">
            <b-shortcut :menus="navItems"></b-shortcut>
          </div>
        </b-dropdown>
      </b-navbar-nav>
      <b-navbar-nav class="d-md-down-none ml-auto">
        <b-account/>
      </b-navbar-nav>
    </AppHeader>
    <div class="app-body">
      <AppSidebar mobile display="md">
        <SidebarHeader/>
        <SidebarForm/>
        <SidebarNav :navItems="navItems"></SidebarNav>
        <SidebarFooter/>
        <SidebarMinimizer/>
      </AppSidebar>

      <main class="main">
        <Breadcrumb :list="list" v-if="showBreadcrumb"/>
        <div style="height:70px;"></div>
        <div class="container-fluid">
          <router-view></router-view>
        </div>
      </main>
    </div>
