<script>
import {
  Header as AppHeader,
  SidebarToggler,
  Sidebar as AppSidebar,
  SidebarFooter,
  SidebarForm,
  SidebarHeader,
  SidebarMinimizer,
  SidebarNav,
  AsideToggler,
  Footer as AppFooter
  // Breadcrumb
} from "@coreui/vue";

import Aside from "../components/aside";
import Dropdown from "../components/dropdown";
import Breadcrumb from "../components/breadcrumb";
import bAccount from "../components/acc-admin";
import bShortcut from "../components/shortcut-menus";
import oAuth from "../apis/auth.js";

export default {
  components: {
    AppHeader,
    AppSidebar,
    AppFooter,
    SidebarToggler,
    SidebarHeader,
    SidebarFooter,
    SidebarForm,
    SidebarMinimizer,
    SidebarNav,
    AsideToggler,
    Breadcrumb,
    Dropdown,
    bAccount,
    bShortcut
  },
  data() {
    return {
      navItems: [],
      showBreadcrumb: 1
    };
  },
  created: async function() {
    const { meta } = this.$route;
    if (meta && meta.hideBreadcrumb) {
      this.showBreadcrumb = 0;
    } else {
      this.showBreadcrumb = 1;
    }
    this.navItems = await oAuth.menus("adm");
    this.navItems.push({ name: "Logout", icon: "fa fa-sign-out", url: '/signout' });
  },
  mounted() {
    this.hideSidebar();
  },
  computed: {
    name() {
      return this.$route.name;
    },
    list() {
      return this.$route.matched.filter(
        route => route.name || route.meta.label
      );
    }
  },
  watch: {
    $route(to, from) {
      const { meta } = to;
      if (meta && meta.hideBreadcrumb) {
        this.showBreadcrumb = 0;
      } else {
        this.showBreadcrumb = 1;
      }
    }
  },
  methods: {
    hideSidebar() {
      document.body.classList.remove("sidebar-lg-show");
      document.body.classList.remove("sidebar-show");
    },
  }
};
</script>
