import Vue from 'vue';
import VueBootstrap from 'bootstrap-vue';
import VueAxios from 'vue-axios';
import VueNumeralFilterInstaller from 'vue-numeral-filter';
import VueMoment from 'vue-moment';
import VueDatePicker from 'vue-bootstrap-datetimepicker';
import axios from 'axios';
import toastr from "mini-toastr";

import App from './App.vue';
import store from './store';
import routes from './routes';
import token from './apis/token';

toastr.init();
toastr.setIcon('error', 'i', { 'class': 'fa fa-times-circle' });
toastr.setIcon('warn', 'i', { 'class': 'fa fa-warning' });
toastr.setIcon('info', 'i', { 'class': 'fa fa-info-circle' });
toastr.setIcon('success', 'i', { 'class': 'fa fa-check-circle-o' });

Vue.use(VueBootstrap);
Vue.use(VueBootstrap);
Vue.use(VueAxios, axios);
Vue.use(VueNumeralFilterInstaller, { locale: 'en-gb' });
Vue.use(VueDatePicker);
Vue.use(VueMoment);

axios.defaults.baseURL = process.env.VUE_APP_BASE_API;
axios.defaults.headers['Content-type'] = 'application/json';
axios.defaults.headers.common['Access-Control-Allow-Origin'] = true;

global.Vue = Vue;

import 'pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css';

(async() => {
    token.validate();
    const router = await routes.instance();

    return new Vue({
        router,
        store,
        render: h => h(App),
        created: function() {
            token.refresh(1000 * 60 * 30);
            window.router = router;
        }
    }).$mount('#app');
})();

window._store = store;