<template>
  <AppHeaderDropdown right no-caret class="mr-4">
    <template slot="header">
      <span>
        {{user.user_fullname}}
        <i class="ml-2 fa fa-user-circle-o"></i>
      </span>
    </template>
    <template slot="dropdown">
      <b-dropdown-header tag="div" class="thin-border">
        <strong>Hi, {{user.user_name}}</strong>
      </b-dropdown-header>
      <b-dropdown-item
        @click="linkClick(menu)"
        class="thin-border"
        v-for="(menu, idx) in menus"
        :key="idx"
      >{{menu.title}}</b-dropdown-item>
      <b-dropdown-item @click="signout">Sign Out</b-dropdown-item>
    </template>
  </AppHeaderDropdown>
</template>

<style lang="scss" scoped>
.thin-border {
  border-bottom: 1px solid rgba(0, 0, 0, 0.04);
}
.dropdown-item {
  padding: 8px 20px;
  color: #73818f;
}
</style>

<script>
import { HeaderDropdown as AppHeaderDropdown } from "@coreui/vue";
import oAuth from "../apis/auth.js";

export default {
  props: {
    menus: {
      default() {
        return [];
      },
      type: Array
    }
  },
  components: {
    AppHeaderDropdown
  },
  data: () => {
    return { user: {} };
  },
  created: async function() {
    this.user = await oAuth.me();
  },
  methods: {
    linkClick(menu) {
      if (menu.name) this.$router.push({ name: menu.name });
    },
    signout() {
      oAuth.logout();
    }
  }
};
</script>
