<template>
    <div class="b-toolbar">
        <b-button variant="primary" :key="index" @click="$emit('click', item)" v-for="(item, index) in list">
            <i :class="item.icon"></i>
            {{item.text}}
        </b-button>
    </div>
</template>

<script>
    export default {
        props: {
            list: {
                type: Array,
                required: true,
                default: () => []
            }
        }
    };
</script>