import axios from 'axios';
import helpers from './helpers';

function list(options) {
    return new Promise(async(resolve, reject) => {
        const obj = options || {};
        try {
            const keys = ['categId', 'page', 'limit', 'sortBy', 'sortDir', 'filter'];

            let str = '';
            for (const key in obj) {
                if (keys.indexOf(key) >= 0 && obj[key]) {
                    str += str ? '&' : '?';
                    str += key + '=' + encodeURIComponent(obj[key]);
                }
            }

            const url = `/activities/list${str}`;
            const { data } = await axios.get(url);
            const { meta = {}, list } = data;
            obj.total = meta.total;
            resolve(list);
        } catch (error) {
            reject(error);
        }
    });
}

function getDetail(id) {
    return new Promise(async(resolve, reject) => {
        try {
            const url = `/regions/${id}`;
            const { data } = await axios.get(url);
            resolve(data);
        } catch (error) {
            helpers.errorResponse(error, reject);
        }
    });
};

function insert(item) {
    return new Promise(async(resolve, reject) => {
        try {
            const url = '/regions';
            // let keys = ['subj_islab', 'subj_isukg', 'subj_ismulok', 'subj_isactive'];
            // for (var i = 0; i < keys.length; i += 1) {
            //     let key = keys[i];
            //     item[key] = item[key] ? 1 : 0;
            // };

            const { data } = await axios.post(url, item);
            resolve(data);
        } catch (error) {
            helpers.errorResponse(error, reject);
        }
    });
};

function update(id, item) {
    return new Promise(async(resolve, reject) => {
        try {
            const url = `/regions/${id}`;
            delete item.created_at;
            // let keys = ['subj_islab', 'subj_isukg', 'subj_ismulok', 'subj_isactive'];
            // for (var i = 0; i < keys.length; i += 1) {
            //     let key = keys[i];
            //     item[key] = item[key] ? 1 : 0;
            // };

            const { data } = await axios.put(url, item);
            resolve(data);
        } catch (error) {
            helpers.errorResponse(error, reject);
        }
    });
};

function remove(id) {
    return new Promise(async(resolve, reject) => {
        try {
            const url = `/regions/delete_parent/${id}`;
            const { data } = await axios.delete(url);
            resolve(data);
        } catch (error) {
            helpers.errorResponse(error, reject);
        }
    });
};

export default { list, getDetail, insert, update, remove }