<template>
  <div class="animated fadeIn">
    <c-toolbar :list="toolbars" @click="tbrClick"></c-toolbar>
        <b-card no-body="">
          <div slot="header">
            <strong>Data Kota</strong>
          </div>
          <b-card-body>   
              <b-form novalidate >
                  <b-form-row class="b-form-group form-group">
                      <label class="col-md-2 col-form-label">Provinsi</label>
                      <b-col class="col-md-6">
                          <b-form-select v-model="data.region_parent" :options="regionParent"/>
                      </b-col>
                  </b-form-row>
                  <b-form-row class="b-form-group form-group">
                      <label class="col-md-2 col-form-label">Nama Kota</label>
                      <b-col class="col-md-6">
                          <b-form-input id="region_name"
                                        type="text"
                                        v-model.lazy.trim="$v.data.region_name.$model"
                                        :state="chkState('region_name')"
                                        aria-describedby="regionNameFeedback"
                                        placeholder="Nama Kota"
                                        />
                          <b-form-invalid-feedback id="storeNameFeedback">
                            data wajib diisi, minimal karakter: 3 dan maksimal 100
                          </b-form-invalid-feedback>
                      </b-col>
                  </b-form-row>
              </b-form>
            <br/>
          </b-card-body>
        </b-card>
  </div>
</template>

<script>
import toastr from "mini-toastr";
import cToolbar from "@/components/toolbar";
import apiRegions from "../../../apis/regions";
import { globalStore } from '../../../apis/constant';

import { validationMixin } from "vuelidate"
import { required, minLength, maxLength, sameAs, helpers } from "vuelidate/lib/validators"


const formShape = {
  region_id: null,
  region_name: '',
  region_parent: null
}

export default {
  name: "student",
  components: { cToolbar },
  data() {
    return {
      toolbars: [
        { text: "Simpan", icon: "fa fa-save", action: "save" },
        { text: "Batal", icon: "fa fa-undo", action: "cancel" }
      ],
      data: {},
      regionParent: {},
      feedBack: 'secondary',
      submitted: false,
      model: {}
    }
  },
  created: async function() {
      const { $route } = this;
      const { params } = $route;
      this.onReset();
      this.regionParent = await apiRegions.dropDownProvince();
      if (params.region_id > 0) {
        this.data = await apiRegions.getDetail(params.region_id);
      } else {
          this.data.region_id = 0;
          this.data.region_parent = 1;
      }

      toastr.init();
  },
  computed: {
    formStr() { return JSON.stringify(this.data, null, 4) },
    isValid() { return !this.$v.data.$anyError },
    isDirty() { return this.$v.data.$anyDirty },
    invCheck() { return 'You must accept before submitting' },
  },
  mixins: [validationMixin],
  validations: {
    data: {
      region_id: {},
      region_name: {
        required,
        minLength: minLength(3),
        maxLength: maxLength(100)
      },
      region_parent: {
        required
      }
    }
  },
  methods: {
    tbrClick(item) {
      if (item.action) {
        this[item.action]();
      }
    },
    async save() {
        if (!this.validate()) {
            toastr.error('Lengkapi data terlebih dahulu', "Update Error");
            return;
        };

        if (this.data.region_id === 0) {
          try {
            await this.insert();
          } catch (error) {
              toastr.error(error.message, "Error");
          }
        } else {
            this.update();
        }
    },
     insert: async function() {
        const item = JSON.parse(JSON.stringify(this.data))
        const id = item.store_id;
        item.region_categ = 'R2';

        try {
            const detail = await apiRegions.insert(item);
            if (detail.success) {
                toastr.success('Data berhasil disimpan', globalStore.SAVE_CAPTION);
                const { $router } = this;
                $router.push({
                name: "admin.master.region"
                });
            }
        } catch (error) {
            toastr.error(error.message, globalStore.SAVE_ERROR_CAPTION);
        }
    },
    update: async function() {
        const item = JSON.parse(JSON.stringify(this.data))
        
        try {
            const detail = await apiRegions.update(item.region_id, item);
            if (detail.success) {
                toastr.info('Data berhasil disimpan', globalStore.UPDATE_CAPTION);
                const { $router } = this;
                $router.push({
                  name: "admin.master.region"
                });
            }
        } catch (error) {
            toastr.error(error.message, globalStore.UPDATE_ERROR_CAPTION);
        }
    },
    
    cancel() {
        const { $router } = this;
        $router.push({
          name: "admin.master.region"
        });
    },
    onReset() {
      // Reset validation
      this.data = Object.assign({}, formShape)
      this.submitted = false
      this.$nextTick(() => {
        this.$v.$reset()
        this.feedBack = 'secondary'
      })
    },
    chkState(val) {
      const field = this.$v.data[val]
      return !field.$dirty || !field.$invalid
    },
    findFirstError(component = this) {
      if (component.state === false) {
        if (component.$refs.input) {
          component.$refs.input.focus()
          return true
        }
        if (component.$refs.check) {
          component.$refs.check.focus()
          return true
        }
      }
      let focused = false
      component.$children.some((child) => {
        focused = this.findFirstError(child)
        return focused
      })

      return focused
    },
    validate() {
      this.$v.$touch()
      this.$nextTick(() => this.findFirstError())
      return this.isValid
    }
  }
}
</script>

<style scoped>
.btn.disabled {
  cursor: auto;
}
</style>
