<template>
  <div>
    <date-picker :value="value" v-model="model" :config="{format:format}"></date-picker>
    <i style="position:absolute;right:16px;top:10px;" class="fa fa-calendar-check-o"></i>
  </div>
</template>

<script>
export default {
  props: {
    value: null,
    format: {
      default: "YYYY-MM-DD Z"
    }
  },
  data() {
    return { model: this.value };
  },
  watch: {
    model: function(value) {
      this.$emit("input", value);
    },
    value: function(value) {
      this.model = value;
    }
  },
  methods: {}
};
</script>