<template>
  <AppHeaderDropdown right no-caret class="mr-4">
    <template slot="header">
      <span>
        <i class="fa fa-user-circle-o"></i>
        {{user.fullname}}
      </span>
    </template>
    <template slot="dropdown">
      <b-dropdown-header tag="div" class="thin-border">
        <strong>Hi, {{user.username}}</strong>
      </b-dropdown-header>
      <b-dropdown-item @click="linkClick(menu)" class="thin-border" v-for="(menu, idx) in menus" :key="idx">{{menu.title}}</b-dropdown-item>
      <b-dropdown-item @click="signout">Sign Out</b-dropdown-item>
    </template>
  </AppHeaderDropdown>
</template>

<style lang="scss" scoped>
.thin-border {
  border-bottom: 1px solid rgba(0, 0, 0, 0.04);
}
.dropdown-item {
  padding: 8px 20px;
  color: #73818f;
}
</style>

<script>
import { HeaderDropdown as AppHeaderDropdown } from "@coreui/vue";
import auth from "../apis/auth.js";

export default {
  components: {
    AppHeaderDropdown
  },
  data: () => {
    return {
      menus: [],
      user: {
        username: "wsugiri",
        email: "wsugiri@mail.com"
      }
    };
  },
  created: async function() {
    this.menus = [
      { title: "Profile", name: "app.profile" }
      // { title: "Setting" },
      // { title: "Favorit" }
    ];
    this.user = await auth.me();
  },
  methods: {
    linkClick(menu) {
      if (menu.name) this.$router.push({ name: menu.name });
    },
    signout() {
      localStorage.clear();
      // this.$router.push("/");
      location.href = "/";
    }
  }
};
</script>
