function errorResponse(error, reject) {
    if (error.response && error.response.data) {
        reject(error.response.data);
    } else {
        reject(error);
    }
}

export default {
    menus() {
        return new Promise((resolve, reject) => {
            try {
                const list = [
                    { text: 'Home', name: 'public.home', icon: 'fa fa-home' },
                    // { text: 'Artikel', name: 'public.artikel', icon: 'fa fa-leaf' },
                    // { text: 'Galery', name: 'public.galeri', icon: 'fa fa-folder' },
                    // { text: 'Promo', name: 'public.promo', icon: 'fa fa-heart' },
                    // { text: 'Tentang FieldForce', name: 'public.about', icon: 'fa fa-user' }
                ]
                resolve(list);
            } catch (error) {
                errorResponse(error, reject);
            }
        });
    },
}