<script>
import vSelect from "vue-select";
import dropdown from "../data/dropdown";
export default {
  components: {
    vSelect
  },
  data() {
    return {
      model: {},
      data: {
        genders: dropdown.genders,
        religions: dropdown.religions
      }
    };
  },
  created() {
    this.setModel({});
  },
  computed: {
    confirmPasswordState() {
      const { password, passwordConfirm } = this.model;
      if (!passwordConfirm || passwordConfirm != password) return false;
      return true;
    }
  },
  methods: {
    createAccount: async function() {
      const model = this.getModel();
      if (model) {
        try {
          await this.$http.post("auth/register", model);
          this.$router.push("/login");
        } catch (err) {
          console.log(err);
        }
      }
    },
    getModel() {
      const model = JSON.parse(JSON.stringify(this.model));

      const invalid = document.querySelector(".is-invalid");
      if (invalid) {
        invalid.focus();
        return;
      }

      if (model.passwordConfirm) delete model.passwordConfirm;
      if (model.gender) model.gender = model.gender.value;
      if (model.religion) model.religion = model.religion.value;
      return model;
    },
    setModel(model) {
      for (const key in model) {
        this.model[key] = model[key];
      }

      if (model.gender) {
        this.model.gender = this.data.genders.find(
          m => m.value == model.gender
        );
      }

      if (model.religion) {
        this.model.religion = this.data.religions.find(
          m => m.value == model.religion
        );
      }
    },
    demoData() {
      const model = {
        address1: "Griya Soka Bogor Raya",
        address2: "Blok I No 22",
        bank_name: "BCA",
        bank_rekno: "012761826182",
        email: "wsugiri@yopmail.com",
        fullname: "Wawan Sugiri",
        gender: "M",
        mobileno: "190281288882",
        religion: "I"
      };

      this.setModel(model);
    }
  }
};
</script>
