<template>
  <b-card :header="caption">
    <b-table :hover="hover" :striped="striped" :bordered="bordered" :small="small" :fixed="fixed" responsive="sm" :items="items" :fields="fields" :current-page="currentPage" :per-page="perPage">
    </b-table>
    <nav>
      <b-pagination :total-rows="getRowCount(items)" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
    </nav>
  </b-card>
</template>

<script>
export default {
  name: "c-table",
  props: {
    caption: {
      type: String,
      default: "Table"
    },
    hover: {
      type: Boolean,
      default: false
    },
    striped: {
      type: Boolean,
      default: false
    },
    bordered: {
      type: Boolean,
      default: false
    },
    small: {
      type: Boolean,
      default: false
    },
    fixed: {
      type: Boolean,
      default: false
    },
    data: {
      type: Object,
      default: () => {}
    }
  },
  data: () => {
    return {
      perPage: 5,
      currentPage: 1,
      totalRows: 0,
      fields: []
    };
  },
  methods: {
    getRowCount(items) {
      return items ? items.length : 0;
    }
  },
  mounted() {
    const {
      fields,
      currentPage = 1,
      totalRows = 0,
      perPage = 5,
      items
    } = this.data;
    this.fields = fields;
    this.currentPage = currentPage;
    this.totalRows = totalRows;
    this.perPage = perPage;
  },
  computed: {
    items: function() {
      return this.data.items;
    }
  }
};
</script>
