import axios from 'axios';
import helpers from './helpers';

function list(options) {
    return new Promise(async(resolve, reject) => {
        const obj = options || {};
        try {
            const keys = ['page', 'limit', 'sortBy', 'sortDir', 'filter'];

            let str = '';
            for (const key in obj) {
                if (keys.indexOf(key) >= 0 && obj[key]) {
                    str += str ? '&' : '?';
                    str += key + '=' + encodeURIComponent(obj[key]);
                }
            }

            const url = `/stores/list${str}`;
            const { data } = await axios.get(url);
            const { meta = {}, list } = data;
            obj.total = meta.total;
            resolve(list);
        } catch (error) {
            reject(error);
        }
    });
}

function dropDown() {
    return new Promise(async(resolve, reject) => {
        try {
            const { data } = await axios.get('/storecategories/dropdown');
            resolve(data);
        } catch (error) {
            reject(error);
        }
    });
}

function insert(item) {
    return new Promise(async(resolve, reject) => {
        try {
            const url = '/subject';
            let keys = ['subj_islab', 'subj_isukg', 'subj_ismulok', 'subj_isactive'];
            for (var i = 0; i < keys.length; i += 1) {
                let key = keys[i];
                item[key] = item[key] ? 1 : 0;
            };

            const { data } = await axios.post(url, item);
            resolve(data);
        } catch (error) {
            helpers.errorResponse(error, reject);
        }
    });
};

function update(id, item) {
    return new Promise(async(resolve, reject) => {
        try {
            const url = `/subject/${id}`;
            delete item.sch_id;

            let keys = ['subj_islab', 'subj_isukg', 'subj_ismulok', 'subj_isactive'];
            for (var i = 0; i < keys.length; i += 1) {
                let key = keys[i];
                item[key] = item[key] ? 1 : 0;
            };

            const { data } = await axios.put(url, item);
            resolve(data);
        } catch (error) {
            helpers.errorResponse(error, reject);
        }
    });
};

function remove(id) {
    return new Promise(async(resolve, reject) => {
        try {
            const url = `/subject/${id}`;
            const { data } = await axios.delete(url);
            resolve(data);
        } catch (error) {
            helpers.errorResponse(error, reject);
            // reject(error);
        }
    });
};

export default { list, dropDown }