<template>
  <div class="animated fadeIn">
    <c-toolbar :list="toolbars" @click="tbrClick"></c-toolbar>
        <b-card no-body="">
          <div slot="header">
            <strong>Data Toko</strong>
          </div>
          <b-card-body>   
              <b-form novalidate >
                  <b-form-row class="b-form-group form-group">
                      <label class="col-md-2 col-form-label">Nama Toko</label>
                      <b-col class="col-md-6">
                          <b-form-input id="store_name"
                                        type="text"
                                        v-model.lazy.trim="$v.data.store_name.$model"
                                        :state="chkState('store_name')"
                                        aria-describedby="storeNameFeedback"
                                        placeholder="Nama Toko"
                                        />
                          <b-form-invalid-feedback id="storeNameFeedback">
                            data wajib diisi, minimal karakter: 3 dan maksimal 100
                          </b-form-invalid-feedback>
                      </b-col>
                      <label class="col-md-2 col-form-label">Kategori</label>
                      <b-col class="col-md-2">
                          <b-form-select v-model="data.category_id" :options="storeCategories"/>
                      </b-col>
                  </b-form-row>

                  <b-form-row class="b-form-group form-group">
                      <label class="col-md-2 col-form-label">Alamat Toko</label>
                      <b-col class="col-md-10">
                          <b-form-input id="store_address"
                                        type="text"
                                        v-model.lazy.trim="$v.data.store_address.$model"
                                        :state="chkState('store_address')"
                                        aria-describedby="storeAddressFeedback"
                                        placeholder="Alamat Toko"
                                        autofocus />
                          <b-form-invalid-feedback id="storeAddressFeedback">
                            data wajib diisi, minimal karakter: 5 dan maksimal 255
                          </b-form-invalid-feedback>
                      </b-col>
                  </b-form-row>

                  <b-form-row class="b-form-group form-group">
                      <label class="col-md-2 col-form-label">Region</label>
                      <b-col class="col-md-4">
                          <b-form-select v-model="data.region_id" :options="regions" @change="selectArea" />
                          <!-- <div>Selected: <strong>{{ data.region_id }}</strong></div> -->
                      </b-col>

                      <label class="col-md-2 col-form-label">Area Manage</label>
                      <b-col class="col-md-4">
                          <b-form-select v-model="data.area_tmp" :options="areaManage">
                            <template slot="first">
                                    <option :value="null" >-- Pilih Area manage --</option>
                            </template>
                          </b-form-select>
                      </b-col>
                  </b-form-row>
                
              </b-form>
            <br/>
          </b-card-body>
        </b-card>
  </div>
</template>

<script>
import toastr from "mini-toastr";
import cToolbar from "@/components/toolbar";
import apiStore from "../../../apis/store";
import apiStoreCategories from "../../../apis/storecategories";
import apiRegions from "../../../apis/regions";
import { globalStore } from '../../../apis/constant';

import { validationMixin } from "vuelidate"
import { required, minLength, maxLength, sameAs, helpers } from "vuelidate/lib/validators"


const formShape = {
  store_id: null,
  store_name: '',
  store_address: '',
  region_id: 0,
  area_id: 0,
  category_id: 0,
  area_tmp: null
}

export default {
  name: "student",
  // , vSelect 
  components: { cToolbar },
  data() {
    return {
      toolbars: [
        { text: "Simpan", icon: "fa fa-save", action: "save" },
        { text: "Batal", icon: "fa fa-undo", action: "cancel" }
      ],
      data: {},
      storeCategories: {},
      regions: {},
      areaManage: {},
      feedBack: 'secondary',
      submitted: false,
      model: {}
    }
  },
  created: async function() {
      const { $route } = this;
      const { params } = $route;

      this.onReset(); 
      this.storeCategories = await apiStoreCategories.dropDown();
      this.regions = await apiRegions.dropDownCity();

      if (params.store_id.length === 36) {
        this.data = await apiStore.getDetail(params.store_id);
        this.data.area_tmp = this.data.area_id;
        this.getArea(this.data.region_id);
      } else {
        this.data.area_tmp = null;
      }
      toastr.init();
  },
  computed: {
    formStr() { return JSON.stringify(this.data, null, 4) },
    isValid() { return !this.$v.data.$anyError },
    isDirty() { return this.$v.data.$anyDirty },
  },
  mixins: [validationMixin],
  validations: {
    data: {
      store_id: {},
      store_name: {
        required,
        minLength: minLength(3),
        maxLength: maxLength(100)
      },
      store_address: {
        required,
        minLength: minLength(5),
        maxLength: maxLength(255)
      },
      region_id: {
        required
      },
      category_id: {
        required
      },
      area_id: {
        required
      },
      area_tmp: {
        required
      }
    }
  },
  methods: {
    tbrClick(item) {
      if (item.action) {
        this[item.action]();
      }
    },
    async getArea(region_id) {
      this.areaManage = await apiRegions.dropDownArea(region_id);
    },
    selectArea: function(selected) {
      this.data.area_tmp = null;
      this.getArea(selected);
    },
    async save() {
      this.data.area_id = this.data.area_tmp;

        if (!this.validate()) {
            toastr.error('Lengkapi data terlebih dahulu', "Update Error");
            return;
        };

        if (!this.data.store_id || this.data.store_id.length < 36) {
          try {
            await this.insert();
          } catch (error) {
              toastr.error(error.message, "Error");
          }
        } else {
            this.update();
        }
    },
     insert: async function() {
        const item = JSON.parse(JSON.stringify(this.data))
        const id = item.store_id;

        try {
            const detail = await apiStore.insert(item);
            if (detail.success) {
                toastr.success('Data berhasil disimpan', globalStore.SAVE_CAPTION);
                const { $router } = this;
                $router.push({
                name: "admin.master.store"
                });
            }
        } catch (error) {
            toastr.error(error.message, globalStore.SAVE_ERROR_CAPTION);
        }
    },
    update: async function() {
        const item = JSON.parse(JSON.stringify(this.data))
        
        try {
            const detail = await apiStore.update(item.store_id, item);
            if (detail.success) {
                toastr.info('Data berhasil disimpan', globalStore.UPDATE_CAPTION);
                const { $router } = this;
                $router.push({
                  name: "admin.master.store"
                });
            }
        } catch (error) {
            toastr.error(error.message, globalStore.UPDATE_ERROR_CAPTION);
        }
    },
    
    cancel() {
        const { $router } = this;
        $router.push({
          name: "admin.master.store"
        });
    },
    onReset() {
      // Reset validation
      this.data = Object.assign({}, formShape)
      this.submitted = false
      this.$nextTick(() => {
        this.$v.$reset()
        this.feedBack = 'secondary'
      })
    },
    chkState(val) {
      const field = this.$v.data[val]
      return !field.$dirty || !field.$invalid
    },
    findFirstError(component = this) {
      if (component.state === false) {
        if (component.$refs.input) {
          component.$refs.input.focus()
          return true
        }
        if (component.$refs.check) {
          component.$refs.check.focus()
          return true
        }
      }
      let focused = false
      component.$children.some((child) => {
        focused = this.findFirstError(child)
        return focused
      })

      return focused
    },
    validate() {
      this.$v.$touch()
      this.$nextTick(() => this.findFirstError())
      return this.isValid
    }
  }
}
</script>

<style scoped>
.btn.disabled {
  cursor: auto;
}
</style>
