<template>
  <div class="animated fadeIn">
    <b-card>
      <div slot="header">
        <strong>DatePicker</strong>
        <small class="ml-1">v-date-picker</small>
      </div>
      <b-row>
        <b-col md="9">
          <b-form-group horizontal label="Date Picker 1">
            <v-date-picker v-model="model.date1"></v-date-picker>
          </b-form-group>
          <b-form-group horizontal label="Date Picker 2">
            <v-date-picker v-model="model.date2"></v-date-picker>
          </b-form-group>
          <b-form-group horizontal label="Date Picker 3">
            <v-date-picker v-model="model.date3" format="YYYY-MM-DD"></v-date-picker>
          </b-form-group>
        </b-col>
      </b-row>
    </b-card>
    <b-card>
      <div slot="header">
        <strong>DatetimePicker</strong>
        <small class="ml-1">v-date-time-picker</small>
      </div>
      <b-row>
        <b-col md="9">
          <b-form-group horizontal label="Date Time Picker 1">
            <v-date-time-picker v-model="model.datetime1"></v-date-time-picker>
          </b-form-group>
          <b-form-group horizontal label="Date Time Picker 2">
            <v-date-time-picker v-model="model.datetime2"></v-date-time-picker>
          </b-form-group>
          <b-form-group horizontal label="Date Time Picker 3">
            <v-date-time-picker v-model="model.datetime1" format="YYYY-MM-DD HH:mm"></v-date-time-picker>
          </b-form-group>
        </b-col>
      </b-row>
    </b-card>
    <b-card>
      <pre class="card-text">
date1: {{model.date1|moment("YYYY-MM-DD Z")}}
date2: {{model.date2|moment("YYYY-MM-DD Z")}}
date3: {{model.date3|moment("YYYY-MM-DD Z")}}
datetime1: {{model.datetime1|moment("YYYY-MM-DD HH:mm Z")}}
datetime2: {{model.datetime2|moment("YYYY-MM-DD HH:mm Z")}}
datetime3: {{model.datetime3|moment("YYYY-MM-DD HH:mm Z")}}</pre>
    </b-card>
  </div>
</template>

<script>
import vDatePicker from "../../components/datepicker";
import vDateTimePicker from "../../components/datetimepicker";
import moment from "moment";

export default {
  components: { vDatePicker, vDateTimePicker },
  data() {
    return {
      model: {
        date1: moment()
          .add("Day", -19)
          .toDate(),
        date2: new Date(),
        date3: moment()
          .add("Month", 4)
          .add("Day", 14)
          .toDate(),
        datetime1: moment()
          .add("Day", -19)
          .toDate(),
        datetime2: new Date(),
        datetime3: moment()
          .add("Month", 4)
          .add("Day", 14)
          .toDate()
      }
    };
  }
};
</script>
