<template>
  <div class="app">
    <app-header fixed>
      <b-link class="navbar-brand" :to="{name: 'public'}">
        <img class="navbar-brand-full logo" src="img/logo-bbm.png">
      </b-link>
      <ul class="navbar-nav d-md-down-none">
        <li class="nav-item px-3" v-for="(menu, idx) in menus" :key="idx">
          <router-link class="nav-link" :to="{name:menu.name}">
            <i :class="menu.icon"></i>
            {{menu.text}}
          </router-link>
        </li>
      </ul>
      <b-navbar-nav class="ml-auto">
        <b-account v-if="user" :menus="shortcuts"></b-account>
        <b-link v-if="!user" class="mr-4" variant="success" :to="{name:'page.login'}">
          <i class="fa fa-sign-in ml-2 mr-1"></i>
          <span class="mr-2">Sign In</span>
        </b-link>
      </b-navbar-nav>
    </app-header>
    <div class="app-body">
      <main class="main">
        <router-view></router-view>
      </main>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.app-header {
  .logo {
    height: 48px;
  }

  .header-dropdown {
    margin-right: 20px;
  }
  background-color: black;
}

.shortcut-link {
  width: 900px;
  max-width: 900px;
}
</style>

<script>
import { Header as AppHeader } from "@coreui/vue";
import bAccount from "../components/account";
import oPublic from "../apis/public";
import oAuth from "../apis/auth";

export default {
  components: { AppHeader, bAccount },
  data() {
    return {
      menus: [],
      shortcuts: [
        { title: "FieldForce", name: "admin" },
        { title: "My Profile", name: "public.profile" },
      ]
    };
  },
  beforeCreate() {
    this.user = oAuth.me();
  },
  created() {
    this.refresh();
  },
  methods: {
    async refresh() {
      const menus = await oPublic.menus();
      this.menus = menus;
    }
  }
};
</script>
