import Vue from 'vue'
import Router from 'vue-router'
import routes from './data/routes.json';
import store from './store';

Vue.use(Router)

routes.forEach((row) => {
    const length = row.id.split('.').length;
    const children = routes.filter(
        m => m.id.split(".").length === length + 1 && m.id.indexOf(row.id) === 0
    );

    if (children.length > 0) row.children = children;
    if (row.source) {
        row.component = require(`${row.source}`).default;
    } else {
        row.component = { render: c => c('router-view') };
    }

    const meta = row.meta || {};

    if (row.text) {
        meta.label = row.text;
    }
    row.meta = meta;
    if (row.source) row.name = row.id;

    if (row.secured) {
        row.beforeEnter = function (to, form, next) {
            const { user } = store.state;
            if (user && user.roles) {
                if (user.roles.indexOf(row.secured.group) >= 0) {
                    next();
                    return;
                }
            }
            // next({ name: 'page.login' });
            next({ name: 'public' });
        }
    }
});


export default {
    instance: async () => {
        const resp = new Router({
            base: process.env.BASE_URL,
            linkActiveClass: 'open active',
            scrollBehavior: () => ({ y: 0 }),
            routes: routes.filter(row => (row.id.split('.').length == 1))
        })
        return resp;
    }
};