import axios from 'axios';
import store from '../store';

const APP_AUTH_ME = process.env.VUE_APP_AUTH_ME;
const APP_AUTH_TK = process.env.VUE_APP_AUTH_TK;

function errorResponse(error, reject) {
    if (error.response && error.response.data) {
        reject(error.response.data);
    } else {
        reject(error);
    }
}

export default {
    menus(group) {
        return new Promise(async(resolve, reject) => {
            try {
                // const name = group ? '.' + group : '';
                // const menus = require(`../data/menus${name || ''}.json`);
                // resolve(menus);

                const { data } = await axios.get(`auth/menus/${group}`);
                resolve(data.data);
            } catch (error) {
                // reject(error);
                errorResponse(error, reject);
            }
        });
    },
    validate(user) {
        return new Promise(async(resolve, reject) => {
            try {
                const { data: token } = await axios.post("auth/login", user);
                axios.defaults.headers['Authorization'] = token.token;
                resolve(token);
            } catch (error) {
                // reject(error);
                errorResponse(error, reject);
            };
        });
    },
    me() {
        const sdata = localStorage.getItem(APP_AUTH_ME);
        if (sdata) {
            const user = JSON.parse(sdata);
            store.commit('user', user);
            return user;
        }
        return null;
    },
    save(token, user) {
        localStorage.setItem(APP_AUTH_TK, JSON.stringify(token));
        localStorage.setItem(APP_AUTH_ME, JSON.stringify(user));
    },
    current() {
        return new Promise(async(resolve, reject) => {
            try {
                const { data: user } = await axios.get("auth/me");
                resolve(user);
            } catch (error) {
                errorResponse(error, reject);
            };
        });
    },
    logout() {
        localStorage.clear();
        location.href = "/";
    },
};