<template>
<div class="region-list">
  <c-toolbar :list="toolbars" @click="tbrClick"></c-toolbar>
    <div class="animated fadeIn">
        <b-card header="Daftar Area Manage">
            <b-row>
                <b-col lg="6">
                <b-form-group :horizontal="horizontal" label="Pencarian">
                    <b-form-input v-model="arrData.filter" placeholder="Ketik Nama Area"></b-form-input>
                </b-form-group>
                </b-col>
            </b-row>
            <b-table ref="table" striped outlined hover show-empty
                stacked="md"
                :busy.sync="isBusy"
                :items="myProvider" 
                :fields="arrData.fields"
                :current-page="arrData.currentPage"
                :per-page="arrData.perPage"
                :filterName="arrData.filterName"
                :sort-by.sync="arrData.sortBy"
                :sort-desc.sync="arrData.sortDesc"
             >
                <template slot="HEAD_id" slot-scope="data">
                    <div class="text-center">{{data.label}}</div>
                </template>
                <template slot="index" slot-scope="data">
                    {{(data.index + 1) + ((arrData.currentPage - 1) * arrData.perPage)}}
                </template>

                <template slot="action" slot-scope="data">
                  <a href="javascript:void(0)" @click="editData(data.item)">
                      <i class="fa fa-pencil"></i>
                  </a>
                  |
                  <a href="javascript:void(0)" class="text-danger" @click="deleteConfirm(data.item)">
                      <i class="fa fa-trash"></i>
                  </a>
                </template> 
            </b-table>
            <b-row class="mt-2">
              <b-col cols="6">
                <div>Showing
                  {{(arrData.currentPage - 1) * arrData.perPage + 1}} 
                  to 
                  {{(arrData.currentPage * arrData.perPage) > arrData.total ? arrData.total : (arrData.currentPage * arrData.perPage)}}
                  of 
                  {{arrData.total}}
                  entries</div>
              </b-col>
              <b-col cols="6">
                <b-pagination align="right"
                  :total-rows="arrData.total"
                  :per-page="arrData.perPage"
                  v-model="arrData.currentPage">
                </b-pagination>
              </b-col>
            </b-row>
        </b-card>
    </div>

    <b-modal v-model="show" title="Hapus Data?" :centered="true" @ok="deleteData">
        <p class="mb-1">Anda yakin akan menghapus data?</p>
        <p class="font-weight-bold m-0 text-danger">Peringatan: Data yang sudah dihapus tidak dapat dibatalkan.</p>
    </b-modal>

  </div>
</template>

<style lang="scss">
.region-list {
  td {
    padding: 0.35rem;
    a {
      text-decoration: none;
      padding: 0.45rem 0.75rem;
    }
  }

  th {
    padding: 0.5rem;
    background-color:rgb(231, 231, 231);
  }
}
</style>

<script>
import toastr from "mini-toastr";
import cToolbar from "@/components/toolbar";
import apiRegions from "../../../apis/regions";
import { globalStore } from '../../../apis/constant';

export default {
  components: { cToolbar },
  created: async function() {
    toastr.init();
  },
  computed: {
    filter() {
      return this.arrData.filter;
    }
  },
  watch: {
    filter: function(filter) {
      const arrData = this.arrData;
      const refresh = this.refresh;
      setTimeout(() => {
        if (filter == arrData.filter) refresh();
      }, 500);
    }
  },
  methods: {
    tbrClick(item) {
      if (item.action) {
        this[item.action]();
      }
    },
    myProvider: async function(ctx) {
        this.arrData.sortDir = this.arrData.sortDesc ? "desc" : "asc";
        const { perPage, currentPage } = ctx;
        this.arrData.page = currentPage;
        this.arrData.limit = perPage;
        this.arrData.categId = 'R3';
        const list = await apiRegions.list(this.arrData);
        return list;
    },
    refresh() {
      this.$refs.table.refresh();
    },
    newRecord() {
        const { $router } = this;
        $router.push({
          name: "admin.master.areaForm",
          params: { region_id: 0 }
        });
    },
    editData(item) {
        const { $router } = this;
        $router.push({
          name: "admin.master.areaForm",
          params: { region_id: item.region_id }
        });
    },
    deleteConfirm(item) {
        this.item = item;
        this.show = true;
    },
    deleteData: async function() {
      try {
        const detail = await apiRegions.removeArea(this.item.region_id);
        this.refresh();
      } catch (error) {
        toastr.error(error.message, globalStore.UPDATE_ERROR_CAPTION);
      }
        
    }
  },
  data() {
    return {
        horizontal: true,
        striped: false,
        toolbars: [
            { text: "Tambah", icon: "fa fa-plus", action: "newRecord" },
            { text: "Print", icon: "fa fa-print", action: "displayReport" }
        ],
        arrData: {
            fields: [
                {
                    key: "index",
                    class: "prov-index",
                    label: "#",
                    thStyle: "width:50px"
                },
                {
                    key: "region_name",
                    label: "Nama Area",
                    sortable: true
                },
                {
                    key: "parent",
                    label: "Kota",
                    sortable: true
                },
                {
                    key: "action",
                    label: "Detail",
                    thClass: "text-center",
                    tdClass: "text-center",
                    thStyle: "width:100px"
                }
            ],
            currentPage: 1,
            perPage: 10,
            limit: 10,
            total: 0,
            pageOptions: [ 10, 15 ],
            sortBy: null,
            sortDesc: false,
            sortDirection: "asc",
            filter: null
        },
        isBusy: false,
        // modalInfo: { title: '', content: '' },
        show: false
    };
  }
}; 
</script>
